<style scoped>
    .page {
        margin-top: 10px;
    }
</style>
<template>
    <div>
        <Table :columns="columns" :data="data"></Table>
        <Page class="page" @on-change="toPage" :total="allData.length" />
    </div>
</template>
<script>
    import axios from "axios";
    export default {
         data () {
            return {
                columns: [
                    {
                        title: '支付单号',
                        key: 'trade_no'
                    },
                    {
                        title: '充值金额',
                        key: 'recharge_amount'
                    },
                    {
                        title: '充值方式',
                        key: 'recharge_way'
                    },
                    {
                        title: '支付时间',
                        key: 'timestamp'
                    },
                    {
                        title: '支付状态',
                        key: 'trade_status'
                    },
                    {
                        title: '操作',
                        key: 'action',
                        render: (h, params) => {
                            let operat = [];
                            if(params.row.status) {
                                operat.push(h('Button', {
                                    props: {
                                        type: 'primary',
                                        size: 'small'
                                    },
                                    style: {
                                        // marginRight: '5px'
                                    },
                                    on: {
                                        click: () => {
                                            this.show(params.row);
                                        }
                                    }
                                }, '查看'));
                            } else {
                                operat.push(h('Button', {
                                    props: {
                                        type: 'primary',
                                        size: 'small'
                                    },
                                    style: {
                                        // marginRight: '5px'
                                    },
                                    on: {
                                        click: () => {
                                            this.pay(params.row);
                                        }
                                    }
                                }, '支付'));
                            }
                            return h('div',operat);
                        }
                    }
                ],
                data: [
                ],
                page: 0,
                pageTotal: 11,
                allData: []
            }
        },
        methods: {
            toPage(page) {
                // console.log(page);
                let start = (page - 1) * this.pageTotal;
                let end = (page - 1) * this.pageTotal + this.pageTotal;
                this.data = this.allData.sort((a,b) => b.id - a.id).slice(start,end);
            },
            pay(data) {
               location.href = location.href = "http://"+location.hostname+"/home/goToPay?trade_id="+data.id+"&way=web";
            },
            show (data) {

                this.$Modal.info({
                    title: '详细信息',
                    content: `
                        充值前余额：${data.money_before}<br />
                        充值后余额：${data.money_after}<br />
                        支付宝方的单号：${data.voucher}<br />
                        充值金额: ${data.recharge_amount}<br />
                        充值方式: ${data.recharge_way}<br />
                        本地流水单号: ${data.trade_no}<br />
                        充值完成时间: ${data.timestamp}<br />
                        订单支付状态: ${data.trade_status}<br />
                        `
                })
            }
        },
        mounted() {
            axios.get("http://"+location.hostname+"/home/recharge/getOrderByUser").then(data => {
                if(data.data.code==1) {
                    this.allData = data.data.data.map(item => Object.assign(item,{
                        status: item.trade_status,
                        trade_status: item.trade_status ? "已付款" : "未付款"
                    }));
                    this.data = this.allData.sort((a,b) => b.id - a.id).slice(this.page,this.pageTotal);
                } else {
                    console.error(data.data.msg);
                }
             });
        }
    }
</script>