<template>
   <div>
        <Table :columns="columns" :data="data"></Table>
        <Page class="page" @on-change="toPage" :total="allData.length" />
        <Modal
            v-model="renewal_fee"
            title="续费服务器"
            @on-ok="ok"
            @on-cancel="cancel">
            <Select placeholder="请选择续费时长" v-model="renewal_fee_config.price" style="width:100%">
                <Option v-for="item in renewal_fee_config.price_options" :value="item.value" :key="item.value">{{ item.label }}</Option>
            </Select>
        </Modal>
   </div>
</template>
<script>
    import axios from "axios";
    export default {
        data () {
            return {
                renewal_fee: false,
                renewal_fee_config: {
                    price: 1,
                    price_options: [
                        {
                            value: 1,
                            label: "一个月"
                        },
                        {
                            value: 6,
                            label: "半年"
                        },
                        {
                            value: 12,
                            label: "一年"
                        }
                    ]
                },
                columns: [
                    {
                        title: '业务编号',
                        key: 'business_number'
                    },
                    {
                        title: '业务类型',
                        key: 'business_type'
                    },
                    {
                        title: '机器编号',
                        key: 'machine_number'
                    },
                    {
                        title: '业务状态',
                        key: 'business_status'
                    },
                    {
                        title: '单价',
                        key: 'money'
                    },
                    {
                        title: '时长',
                        key: 'length'
                    },
                    {
                        title: '操作',
                        key: 'action',
                        width: 170,
                        align: 'center',
                        render: (h, params) => {
                            let operat = [
                                h('Button', {
                                props: {
                                    type: 'primary',
                                    size: 'small'
                                },
                                style: {
                                    marginRight: '5px'
                                },
                                on: {
                                    click: () => {
                                        this.show(params.row);
                                    }
                                }
                                }, '查看')
                            ];
                            operat.push(h('Button', {
                                    props: {
                                        type: 'primary',
                                        size: 'small'
                                    },
                                    style: {
                                        marginRight: '5px'
                                    },
                                    on: {
                                        click: () => {
                                            this.renewal_fee = true;
                                        }
                                    }
                            }, '续费'));
                            return h('div',operat);
                        }
                    }
                ],
                data: [
                ],
                allData: [],
                page: 0,
                pageTotal: 11,
            };
        },
        methods: {
            ok () {
                // this.$Message.info('Clicked ok');
            },
            cancel () {
                // this.$Message.info('Clicked cancel');
            },
            toPage(page) {
                // console.log(page);
                let start = (page - 1) * this.pageTotal;
                let end = (page - 1) * this.pageTotal + this.pageTotal;
                this.data = this.allData.sort((a,b) => b.id - a.id).slice(start,end);
            },
            show (data) {
                this.$Modal.info({
                    title: '详细信息',
                    content: `
                        业务编号: ${data.business_number}<br />
                        业务类型: ${data.business_type}<br />
                        机器编号: ${data.machine_number}<br />
                        业务状态: ${data.business_status}<br />
                        单价: ${data.money}<br />
                        时长: ${data.length}<br />
                        备注: ${data.business_note}<br />
                        `
                })
            },
            getData() {
                 axios.get("http://"+location.hostname+"/home/customer/businessList").then(res => {
                    if(res.data.code==1) {
                        this.allData = res.data.data;
                        this.data = this.allData.sort((a,b) => b.id - a.id).slice(this.page,this.pageTotal);
                    }else if(res.data.code==5000) {
                        location.href = "http://"+location.hostname+"/tz/login.html";
                    } else {
                        console.error(res.data.msg);
                    }
                });
            }
        },
        mounted() {
           this.getData();
        }
    }
</script>