<style scoped>
.layout{
    height: 100%;
    border: 1px solid #d7dde4;
    background: #f5f7f9;
    position: relative;
    border-radius: 4px;
    overflow: hidden;
}
.layout-container {
    height: 100%;
}
.layout-logo{
    float: left;
    position: relative;
    top: 12px;
    left: 20px;
}
.layout-nav{
    float: right;
    margin: 0 auto;
    margin-right: 0;
}
.userinfo {
    color: #fff;
}
.userinfolist p {
    padding-left: 20px;
}
</style>
<template>
    <div class="layout">
        <Layout class="layout-container">
            <Header>
                <Menu mode="horizontal" theme="dark" active-name="1">
                    <div class="layout-logo">
                        <img src="../resource/images/logo.png" alt="" />
                    </div>
                    <div class="layout-nav">
                       <Dropdown placement="bottom-end" :visible="visible" style="margin-left: 20px">
                        <a href="javascript:void(0)" class="userinfo" @click="handleOpen">
                            <Avatar icon="ios-person" size="large" />
                            {{user.email}}
                            <Icon type="ios-arrow-down"></Icon>
                        </a>
                        <DropdownMenu slot="list" class="userinfolist" style="width: 300px">
                            <p>金额：{{user.money}}</p>
                            <div style="text-align: right;margin:10px;margin-bottom: 0;">
                                <Button @click="rechargeModal = true" style="margin-right: 5px;" type="primary">在线充值</Button>
                                <Modal
                                    v-model="rechargeModal"
                                    title="在线充值"
                                    @on-ok="ok"
                                    @on-cancel="cancel">
                                    <Input v-model="value" placeholder="充值金额" style="width: 100%;" />
                                </Modal>
                                <Button @click="toRechargeRecord">充值记录</Button>
                            </div>
                        </DropdownMenu>
                    </Dropdown>
                    </div>
                </Menu>
            </Header>
            <Layout>
                <Sider hide-trigger :style="{background: '#fff'}">
                    <Menu @on-select="toPage" :active-name="activeName" :style="{minHeight: '100%'}" theme="light" width="auto" :open-names="['1']">
                        <Submenu name="1">
                            <template slot="title">
                                <Icon type="ios-card" />
                                订单管理
                            </template>
                            <MenuItem name="1-1">IDC服务器</MenuItem>
                        </Submenu>
                        <Submenu name="2">
                            <template slot="title">
                                <Icon type="md-laptop" />
                                机器管理
                            </template>
                            <MenuItem name="2-1">IDC服务器</MenuItem>
                        </Submenu>
                    </Menu>
                </Sider>
                <Layout :style="{padding: '0 24px 24px'}">
                    <Content :style="{padding: '24px', overflow: 'auto', minHeight: '280px', background: '#fff'}">
                        <router-view></router-view>
                    </Content>
                </Layout>
            </Layout>
        </Layout>
    </div>
    
</template>
<script>
    import axios from "axios";
    export default {
        data () {
            return {
                visible: false,
                user: {},
                rechargeModal: false,
                value: "",
                activeName: ""
            }
        },
        methods: {
            toPage(name) {
                if(name=="1-1") {
                    location.hash = "/";
                }
                if(name=="2-1") {
                    location.hash = "/machine";
                }
            },
            toRechargeRecord() {
                location.hash = "/rechargeRecord";
            },
            handleOpen () {
                this.visible = true;
            },
            handleClose () {
                this.visible = false;
            },
            ok () {
                if(this.value&&!isNaN(this.value)) {
                    axios.get("http://"+location.hostname+"/home/payIndex",{
                        params: {
                            pay_for: 1,
                            total_amount: this.value,
                            subject: "在线充值",
                            trade_no: Math.ceil(100 + (Math.random() * 1000))
                        }
                    }).then(res => {
                        if(res.data.code==1) {
                            location.href = "http://"+location.hostname+"/home/goToPay?trade_id="+res.data.data+"&way=web";
                        } else {
                            this.rechargeModal = true;
                            alert(res.data.msg);
                            //  this.$Message.info('充值失败');
                        }
                    });
                } else {
                    alert("请填写正确的金额");
                }
            },
            cancel () {
                this.$Message.info('需要的时候随时呼唤我');
            }
        },
        mounted() {
            if ("onhashchange" in window) {            
                window.onhashchange = (ev) => {       
                    if(location.hash == "#/") {
                        this.activeName = "1-1";
                    } else if(location.hash == "#/machine") {
                        this.activeName = "2-1";
                    } else {
                        this.activeName = "";
                    }
                };        
            }
             axios.get("http://"+location.hostname+"/home/user/getInfo").then(data => {
                if(data.data.code==1) {
                    this.user = data.data.data;
                } else if(data.data.code==5000) {
                    location.href = "http://"+location.hostname+"/tz/login.html";
                } else {
                    console.error(data.data.msg);
                }
             });
        }
    }
</script>